.galeria {
    padding: $separacion 0;
    .galeria__img {
        list-style: none;
        padding: 0;
        
        @include grid(2);
        justify-content: center;
        align-items: center;
        margin: 0 auto;

        @include tablet {
            @include grid(4);
        }

        @include desktop {
            @include grid(6);
        }
    }

    li {
        margin: 0 auto;
    }

    img {
        margin: 0 auto;

        &:hover {
            box-shadow: 0px 0px 10px 2px rgba(255,67,50,0.5);
            cursor: pointer;
        }

    }
    
}

.overlay {
    position: fixed;
    background-color: rgba($color: $colorNegro, $alpha: 0.7) ;
    width: 100%;
    height: 100%;
    top: 0;
    
    display: grid;
    place-items: center;    // centrar

    /* ACHICO IMAGEN PARA PODER VER EL FONDO */
    img {
        width: 70%;
    }

    .btn-cerrar {
        font-size: 3rem;
        font-weight: $fuente_bold;
        background-color: $colorGrisClaro;
        padding: 1rem;
        border-radius: 50%;
        place-items: center;

        &:hover {
            cursor: pointer;
        }
    }

}
