.header {
    background-color: $colorTealAccent;

    .contenido-header {
        padding: $separacion 0;

        @include tablet {
            display: flex;
            align-items: center;
            justify-content: space-between;
        };

    }


    h1 {
        color: $colorBlanco;
        
    }

    &.fijo {
        position: fixed;
        top: 0;
        width: 100%;
        box-shadow: 0px .2rem .3rem 0px rgba(0,0,0,0.75);
    }

}

.navegacion-principal {

    display: flex;
    justify-content: center;
    gap: $separacion;

    a {
        color: $colorBlanco;
        font-weight: $fuente_bold;
        font-size: 2rem;

        &:hover {   /* & hace referencia al padre respetando anidamiento */
            color: $colorAmarilloAccent;
            transition: 1s;
        }
    }


}

