html {
  font-size: 62.5%;
  /* 1rem = 10px */
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}

@media (min-width: 480px) {
  .contenedor {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .contenedor {
    width: min(85%, 120rem);
  }
}
a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  font-family: "Montserrat Alternates", sans-serif;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 4.2rem;
  font-weight: 900;
}

h2 {
  font-size: 3.8rem;
}
h2:hover {
  color: #ffd600;
  transition: 1s;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

p {
  font-size: 2rem;
  color: #000000;
}

img {
  max-width: 100%;
}

.bg-amarillo {
  background-color: #ffeb3b;
}

.bg-teal {
  background-color: #009688;
}

.header {
  background-color: #00bfa5;
}
.header .contenido-header {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.header h1 {
  color: #FFFFFF;
}
.header.fijo {
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0px 0.2rem 0.3rem 0px rgba(0, 0, 0, 0.75);
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.navegacion-principal a {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  /* & hace referencia al padre respetando anidamiento */
  color: #ffd600;
  transition: 1s;
}

.video {
  position: relative;
  max-height: 30rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(to bottom , $colorRojo, transparent); */
  /* background: radial-gradient(transparent, $colorRojo); */
  background: linear-gradient(to bottom, rgba(255, 67, 50, 0.5) 0%, rgba(255, 255, 255, 0.5) 100%);
}
.video .contenido-video {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  /* subir arriba h2 */
  position: relative;
  z-index: 0;
}
.video .contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* text-align: left; */
  color: #FFFFFF;
  font-weight: 700;
  font-size: 3rem;
}
.video .contenido-video span {
  background-color: #d50000;
  font-weight: 300;
  font-size: 2.4rem;
  padding: 1rem;
}
.video video {
  display: block;
  width: 100%;
}

.sobre-festival {
  margin-top: 1rem;
  /* .sobre-festival__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

  } */
}
@media (min-width: 768px) {
  .sobre-festival {
    /* display: grid; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    justify-content: center;
    align-items: center;
    /* grid-template-columns: repeat(2, 1fr); */
    /* gap: $separacion; */
  }
}
.sobre-festival .sobre-festival__img {
  box-shadow: 0px 0px 10px 2px rgba(255, 67, 50, 0.5);
}
.sobre-festival h2 {
  text-align: left;
}
.sobre-festival h3 {
  color: #d50000;
}

.artistas {
  background-color: #f44336;
  padding: 2rem 0;
  color: #FFFFFF;
}
.artistas h4 {
  text-transform: uppercase;
  margin: 2rem 0;
}
.artistas p {
  font-weight: 700;
  text-align: center;
}
.artistas p:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .artistas .escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.artistas .escenario {
  color: #000000;
  padding: 2rem;
}
.artistas .rock p:after {
  background-image: url("../img/icono_rock.png");
}
.artistas .electronica p:after {
  background-image: url("../img/icono_edm.png");
}
.artistas .calendario {
  list-style: none;
  padding: 0;
}
.artistas .calendario li {
  padding: 1rem;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #ffd600;
}

.bg-teal .calendario li:nth-child(odd) {
  background-color: #00bfa5;
}

.galeria {
  padding: 2rem 0;
}
.galeria .galeria__img {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .galeria .galeria__img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria .galeria__img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galeria li {
  margin: 0 auto;
}
.galeria img {
  margin: 0 auto;
}
.galeria img:hover {
  box-shadow: 0px 0px 10px 2px rgba(255, 67, 50, 0.5);
  cursor: pointer;
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
  /* ACHICO IMAGEN PARA PODER VER EL FONDO */
}
.overlay img {
  width: 70%;
}
.overlay .btn-cerrar {
  font-size: 3rem;
  font-weight: 700;
  background-color: #e9e9e9;
  padding: 1rem;
  border-radius: 50%;
  place-items: center;
}
.overlay .btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  color: #FFFFFF;
  padding: 2rem 0;
}

@media (min-width: 768px) {
  .boletos__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.boletos__contenedor .plan {
  background-color: #f44336;
  margin: 1rem 0;
  padding: 2rem;
}

.pase {
  width: 100%;
  margin: 0 auto;
  border-spacing: 0;
}
.pase thead {
  background-color: #d50000;
}
.pase thead th {
  padding: 2rem;
  text-transform: uppercase;
  font-weight: 300;
}
.pase tbody {
  color: #000000;
  background-color: #FFFFFF;
  font-weight: 700;
  /* padding: 1rem; */
}
.pase tbody tr:nth-child(odd) {
  background-color: #e9e9e9;
}
.pase tbody tr:hover {
  cursor: default;
  background-color: rgba(69, 90, 100, 0.7);
}
.pase tbody tr:last-of-type td {
  border: none;
}
.pase tbody td {
  padding: 1rem;
  border-bottom: 1px solid #455a64;
}
.pase tfoot {
  background-color: #9c27b0;
  text-align: center;
}
.pase tfoot td {
  padding: 2rem;
}

.footer {
  text-align: center;
  background-color: #f44336;
  margin: 0;
  padding: 2rem;
  color: #FFFFFF;
}

.footer__texto {
  margin: 0;
  color: #FFFFFF;
}