.artistas {

    background-color: $colorRojo;
    padding: $separacion 0;
    color: $colorBlanco;

    h4{
        text-transform: uppercase;
        margin: $separacion 0;
    }
    p {
        font-weight: $fuente_bold;
        text-align: center;

        &:after{
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            background-size: 50%;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 0 auto;
        }
    }

    .escenarios {
        @include tablet {
            @include grid(2);
        }
    }

    .escenario {
        color: $colorNegro;
        padding: $separacion;
    }

    .rock p {
        &:after {
            background-image: url('../img/icono_rock.png');
        }
    }

    .electronica p {
        &:after {
            background-image: url('../img/icono_edm.png');
        }
    }

    .calendario {
        list-style: none;
        padding: 0;
        li {
            padding: 1rem;
        }
    }

}

.bg-amarillo .calendario li:nth-child(odd) {
    background-color: $colorAmarilloAccent;
}

.bg-teal .calendario li:nth-child(odd){
    background-color: $colorTealAccent;
}