html {
    font-size: 62.5%;   /* 1rem = 10px */
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: $fuentePrincipal;
    font-size: 1.6rem;
    line-height: 1.5;

    &.fijar-body {
        overflow: hidden;
    }
}

.contenedor {
    width: 95%;
    margin: 0 auto;
}
@media (min-width: 480px) {
    .contenedor {
        width: 90%;
    }
}
@media (min-width: 768px) {
    .contenedor {
        width: min(85%, 120rem);
    }
}

a {
    text-decoration: none;
}

h1, h2, h3, h4 {
    font-family: $fuentePrincipal;
    text-align: center;
    margin: 0;
}
h1 {
    font-size: 4.2rem;
    font-weight: $fuente_black;
}
h2 {
    font-size: 3.8rem;
    &:hover {
        color: $colorAmarilloAccent;
        transition: 1s;
    }
}
h3 {
    font-size: 3.2rem;
}
h4 {
    font-size: 2.8rem;
}

p {
    font-size: 2rem;
    color: $colorNegro;
}

img {
    max-width: 100%;
}