.boletos {
    color: $colorBlanco;
    padding: $separacion 0;
}

.boletos__contenedor {


    @include tablet {
        @include grid(2);
    }

    .plan {
        background-color: $colorRojo;
        margin: 1rem 0;

        padding: $separacion;
    }
}

.pase {
    width: 100%;
    margin: 0 auto;

    border-spacing: 0;

    thead {
        background-color: $colorRojoAccent;
        th {
            padding: $separacion;
            text-transform: uppercase;
            font-weight: $fuente_delgada;
        }
    }

    tbody {
        color: $colorNegro;
        background-color: $colorBlanco;
        font-weight: $fuente_bold;
        /* padding: 1rem; */

        tr:nth-child(odd) {
            background-color: $colorGrisClaro;
        }

        tr {
            &:hover {
                cursor: default;
                background-color: rgba($color: $colorGrisAzulado, $alpha: 0.7);
            }

            &:last-of-type {
                td {
                    border: none;
                }
            }
            
        }

        td {
            padding: 1rem;
            border-bottom: 1px solid $colorGrisAzulado;
        }

    }

    tfoot {
        background-color: $colorMorado;
        text-align: center;

        td {
            padding: $separacion;
        }
    }
}