.video {
    position: relative;
    max-height: 30rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        /* background: linear-gradient(to bottom , $colorRojo, transparent); */
        /* background: radial-gradient(transparent, $colorRojo); */
        background: linear-gradient(to bottom, rgba(255,67,50,0.5) 0%,rgba(255,255,255,0.5) 100%);
    }

    .contenido-video {
        
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        /* subir arriba h2 */
        position: relative;
        z-index: 0; // estaba en 1

        h2 {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            /* text-align: left; */
            color: $colorBlanco;
            font-weight: $fuente_bold;
            font-size: 3rem;
        }

        span {
            background-color: $colorRojoAccent;
            font-weight: $fuente_delgada;
            font-size: 2.4rem;
            padding: 1rem;
        }

    }

    video {
        display: block;
        width: 100%;

    }
}